<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido(data-aos="flip-up")
      .titulo-principal__numero
        span 1
      h1 Sistemas de medidas de calzado
    
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-5(data-aos="zoom-in")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/img1.svg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 La industria del calzado cuenta con distintos sistemas de medidas para determinar la longitud y el volumen del pie, así como las dimensiones de las hormas utilizadas en el proceso de fabricación. Estos sistemas varían según la región del mundo y responden a estándares técnicos específicos. El largo del pie se mide desde el punto más sobresaliente del talón hasta el extremo del dedo más largo.
    
    <!-- BLOQUE 1 -->
    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1 Principales sistemas de numeración de calzado

    p.mb-5 A nivel mundial existen varios sistemas para establecer tallas de calzado. A continuación se describen los principales, junto con sus características, bases de medida y diferencias.

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img2.svg" style="max-width: 70px").m-auto
      .col
        h3 Sistemas métrico - <em>Mondopoint</em>

    p.mb-4.espacio___movil_1 Este sistema fue estandarizado por el Instituto Colombiano de Normas Técnicas (ICONTEC) mediante la norma 1639.

    .row.mb-5.align-items-center
      .col-xl
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Uso
          .col-lg.estilo___caja_3_1
            p.mb-0 Internacional, principalmente en calzado militar, deportivo y técnico.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Medida base
          .col-lg.estilo___caja_3_1
            p.mb-0 Longitud del pie en milímetros.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Características	
          .col-lg.estilo___caja_3_1
            p.mb-0 Se basa en la medida real del pie. Puede incluir también el ancho (por ejemplo, 260/100 indica 260 mm de largo y 100 mm de ancho). Recomendado por la Organización Internacional de Normalización (ISO).
        .row.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Diferencias
          .col-lg.estilo___caja_3_1
            p.mb-0 Es el sistema más preciso, pero menos usado en el comercio minorista. No depende del diseño del zapato.
      .col-xl-auto
        figure(data-aos="fade-left").d-none.d-xl-block
          img(src="@/assets/curso/temas/img3.svg", alt="" style="max-width: 500px").m-auto

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img2.svg" style="max-width: 70px").m-auto
      .col
        h3 Sistema europeo o francés (EU o Paris <em>Point</em>) 

    p.mb-4.espacio___movil_1 Este sistema es ampliamente utilizado en Europa y otras regiones.

    .row.mb-5.align-items-center
      .col-xl-auto
          figure(data-aos="fade-right").d-none.d-xl-block
            img(src="@/assets/curso/temas/img4.svg", alt="" style="max-width: 500px").m-auto
      .col-xl
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Uso
          .col-lg.estilo___caja_3_1
            p.mb-0 Europa y gran parte del mundo.
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Medida base
          .col-lg.estilo___caja_3_1
            p.mb-0 Longitud de la horma en puntos franceses (1 punto = 6.66 mm).
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Características	
          .col-lg.estilo___caja_3_1
            p.mb-0 Mide la longitud de la horma, no del pie. No diferencia entre tallas de hombres, mujeres y niños. No contempla el ancho del pie.
        .row.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Diferencias
          .col-lg.estilo___caja_3_1
            p.mb-0 El incremento entre tallas es de 6.66 mm. Es el sistema más común entre marcas europeas.

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img2.svg" style="max-width: 70px").m-auto
      .col
        h3 Sistema inglés (UK)

    p.mb-4.espacio___movil_1 Se utiliza principalmente en el Reino Unido y países de la <em>Commonwealth</em>.

    .row.mb-5.align-items-center
      .col-xl
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Uso
          .col-lg.estilo___caja_3_1
            p.mb-0 Reino Unido, India, países de la <em>Commonwealth</em>.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Medida base
          .col-lg.estilo___caja_3_1
            p.mb-0 Longitud de la horma en pulgadas.
        .row.mb-2.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Características	
          .col-lg.estilo___caja_3_1
            p.mb-0 Similar al sistema estadounidense, pero con punto de inicio distinto. Usa 4 pulgadas como referencia para la talla 0. Incremento entre tallas de 1/3 de pulgada (8.46 mm).
        .row.m-0(data-aos="fade-right")
          .col-lg-4.estilo___caja_3
            p.mb-0 Diferencias
          .col-lg.estilo___caja_3_1
            p.mb-0 Las tallas UK son una unidad menos que las tallas US (ejemplo: UK 8 = US 9). Tiene menos variación entre tallas masculinas y femeninas.
      .col-xl-auto
        figure(data-aos="fade-left").d-none.d-xl-block
          img(src="@/assets/curso/temas/img5.svg", alt="" style="max-width: 500px").m-auto

    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img2.svg" style="max-width: 70px").m-auto
      .col
        h3 Sistema americano y canadiense (US/CA)

    p.mb-4.espacio___movil_1 Utilizado en Estados Unidos y Canadá, tiene diferencias notables respecto al sistema inglés.

    .row.align-items-center
      .col-xl-auto
          figure(data-aos="fade-right").d-none.d-xl-block
            img(src="@/assets/curso/temas/img6.svg", alt="" style="max-width: 500px").m-auto
      .col-xl
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Uso
          .col-lg.estilo___caja_3_1
            p.mb-0 Estados Unidos y Canadá.
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Medida base
          .col-lg.estilo___caja_3_1
            p.mb-0 Longitud de la horma en pulgadas.
        .row.mb-2.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Características	
          .col-lg.estilo___caja_3_1
            p.mb-0 Existen tallas separadas para hombres, mujeres y niños. Se basa en el sistema inglés, pero inicia 2,116 mm antes. Incremento entre tallas: 1/3 de pulgada (8.46 mm).
        .row.m-0(data-aos="fade-left")
          .col-lg-4.estilo___caja_3
            p.mb-0 Diferencias
          .col-lg.estilo___caja_3_1
            p.mb-0 Para hombres, la talla 0 equivale a 7 2/3 pulgadas. Para mujeres, las tallas son 1.5 unidades más altas que en hombres (Ejemplo: US 8M = US 9.5W).
      
