    Separador
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2 Conversión básica entre sistemas

    p.mb-4 Existen fórmulas que permiten convertir tallas entre los distintos sistemas. Las siguientes equivalencias son aproximadas y pueden variar según el fabricante.

    .titulo-sexto.color-acento-contenido.mb-3(data-aos="fade-right")
      h5 Tabla 1. 
      span Conversión de tallas de calzado

    .row.mb-5.align-items-center
