                    th Fórmula
                tbody
                  tr
                    td.ajuste-border-tabla.texto-left <strong>EU a US (Hombres)</strong>
                    td EU - 33 = US
                  tr.ajuste-color-tabla
                    td.ajuste-border-tabla.texto-left <strong>EU a US (Mujeres)</strong>
                    td EU - 31 = US
                  tr
                    td.ajuste-border-tabla.texto-left <strong>US a UK</strong>
                    td US - 1 = UK
                  tr.ajuste-color-tabla
                    td.ajuste-border-tabla.texto-left <strong><em>Mondopoint</em> a EU</strong>
                    td <em>Mondopoint</em> ÷ 6.66 = EU

          .col-lg.ajuste___caja_3(data-aos="fade-up").h-100
            h4.mb-3.estilo-h4____3 Ejemplos:
            <ul class="fa-ul estilo___ul">
              <li class="mb-3"><span class="fa-li"><i class="fas fa-cut color___fa-cut"></i></span>Talla EU 42 ≈ US 9 (Hombres) / US 10.5 (Mujeres) / UK 8.</li>
              <li><span class="fa-li"><i class="fas fa-cut color___fa-cut"></i></span><em>Mondopoint</em> 270 mm ≈ EU 42.</li>
            </ul>
      .col-xl-auto
        figure(data-aos="fade-left").d-none.d-xl-block
          img(src="@/assets/curso/temas/img7.svg", alt="" style="max-width: 295px")

    p.mb-4 Para mayor precisión, se recomienda utilizar las tablas oficiales proporcionadas por cada marca, ya que la horma puede variar entre fabricantes. Además, no solo debe considerarse la longitud, sino también el ajuste general del calzado, sobre todo en modelos deportivos o de seguridad.
    
