    figure.estilo___caja_4.mb-5
      img(data-aos="zoom-in")(src="@/assets/curso/temas/img9.svg", alt="Diagrama del contorno de un pie con líneas y flechas que indican tres zonas: metatarsos (zona delantera), empeine (zona media elevada) y entrada (zona trasera donde el pie entra al calzado). " style="max-width: 800px").m-auto
    
